@import url(https://fonts.googleapis.com/css?family=Roboto);

.list {
    padding:10px 10px 10px 34px;
    height: auto; 
    margin: auto;
    margin-bottom: 8px;
    width: 60%;
    font-family: 'Lato', sans-serif;
    font-size: 1.4em;
    color: #222;
    background-color: #eed; 
    border-radius: 3px;
    box-shadow: 43x 3px 2px #333;
    opacity: 1;
  }
  
  @-webkit-keyframes fadeIn { from { opacity:0; } to { opacity:0.8; } }
  
  @keyframes fadeIn { from { opacity:0; } to { opacity:0.8; } }
  @-webkit-keyframes fadeOut { from { opactiy:0.8; } to { opacity:0; } }
  @keyframes fadeOut { from { opactiy:0.8; } to { opacity:0; } }
  
  .fade-in {
      opacity:0;
      -webkit-animation:fadeIn ease-in 1;
              animation:fadeIn ease-in 1;
      -webkit-animation-fill-mode:forwards;
              animation-fill-mode:forwards;
      -webkit-animation-duration: 0.1s;
              animation-duration: 0.1s;
  }
  
  .listContent{
    float:left;
    padding-right: 70px;
    padding-bottom: 10px;
    word-break: break-word;
    display: list-item;
  }
  
  .closebtn {
      color: #AA9;
      font-weight: bold;
      float: right;
      cursor: pointer;
      font-size: 1.3em;
  }
  
  .closebtn:hover {
      color: #AC2500;
  }
.formWrapper{
    margin: auto;
}

.listButton{
    border: none;
    margin: 0px;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    padding: 8px 24px;
    font-size: 1.3em;
    font-family: 'Droid Sans', sans-serif;
    background-color: #019875;
    color: #fff;
    -webkit-transition: 0.2s ease-out;
    transition: 0.2s ease-out;
}

.listButton:hover{
    background-color: #02BA85;
    color: #fff;
    -webkit-transition: 0.2s ease-out;
    transition: 0.2s ease-out;
}

.listInput{
    width: 800px;
    min-width: 800px;
    font-size: 1.3em;
    padding-left: 20px;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    font-family: 'Droid Sans', sans-serif;
    background-color: rgba(34,49,63,0.8);
    color: #fff;
    -webkit-transition: 0.2s ease-out;
    transition: 0.2s ease-out;
}

.listInput:focus{
    background-color: #E4F1FE;
    color: #111;
    -webkit-transition: 0.2s ease-in;
    transition: 0.2s ease-in;
}
html{
    height: 100%;
  }
  
  body {
    width: 100%;
    min-height:100%;
    background-attachment: fixed;
    background-size: cover; 
  }
  
  * {
    height: 100%;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    border: 0;
  }
  
  .listsWrapper {
    display: flex;
    flex: 1 1 auto;
    flex-direction: column;
    height: 100%;
  }
  
  .listsHeader {
    display: flex;
    flex: 1 1 5%;
    min-height: 1.7em;
    max-height: 2em;
    font-size: 3.2em;
    color: rgb(165, 206, 112);
    font-family: 'Roboto', cursive;
    text-align: center;
    align-content: center;
    padding:5px 0px 20px 0px;
    margin-bottom: 2px;
    text-shadow: 3px 3px 10px #000;
    display: block;
  }
  
  .listsBody {
    padding-top: 24px;
    display: flex;
    flex: 1 1 85%;
    flex-wrap: wrap;
    align-items: flex-start;
    align-content: flex-start;
    overflow: auto;
  }
  
  .listsFooter {
    display: flex;
    flex: 1 1 10%;
    margin-bottom: 30px;
    
  }
  
